.root {
  width: 100%;
  height: 100%;
}
.lan-layout {
  position: relative;
  .lan-header {
    box-shadow: 0 2px 8px #f0f1f2;
    max-width: 100%;
    background-color: #ffffff;
    .menu-link {
      color: rgba(0,0,0,.65);
      margin: 0 10px;
    }
  }
  .lan-content {
    background-color: #ffffff;
    padding-bottom: 44px;
  }
  .lan-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fafafa;
    width: 100%;
    color: #1b2735;
    font-size: 12px;
    height: 44px;
    line-height: 44px;
    position: fixed;
    bottom: 0;
  }
}